
import React from 'react';
import { th, td, white, purple, yellow } from '../styles';

interface StatisticsPanelProps {
    allCarCount: Record<string, number>;
    preferredOrder: string[];
    handleModelCountClick: (model: string) => void;
    data: any[];
    handleOpenContractsClick: (category: string) => void;
    setTotalCountList: () => void;
}

export const StatisticsPanel: React.FC<StatisticsPanelProps> = ({
    allCarCount,
    preferredOrder,
    handleModelCountClick,
    data,
    handleOpenContractsClick,
    setTotalCountList
}) => {
    return (
        <div style={{
            background: white,
            padding: 16,
            border: `1.5px solid ${purple}`,
            borderRadius: 12,
            minWidth: "320px",
            maxWidth: 500,
            flex: "1 1 40%",
            boxShadow: '0 2px 12px #ffd60022'
        }}>
            {/*  Total of Cars */}
            <div style={{ display: "flex", justifyContent: "center" }}>
                <div style={{
                    background: purple,
                    color: yellow,
                    fontWeight: "bold",
                    fontSize: 17,
                    borderRadius: 24,
                    boxShadow: `0 2px 8px #ffd60044`,
                    border: `1.5px solid ${purple}`,
                    padding: "7px 22px",
                    marginBottom: 8,
                    marginTop: 4,
                    display: "inline-block"
                }}>
                    Total of Cars ( Invygo)
                </div>
            </div>
            <table style={{ borderCollapse: "collapse", width: "100%", marginBottom: 8 }}>
                <thead>
                    <tr>
                        <th style={th}>#</th>
                        <th style={th}>Model</th>
                        <th style={th}>Total Cars</th>
                    </tr>
                </thead>
                <tbody>
                    {Object.entries(allCarCount)
                        .sort(([modelA], [modelB]) => {
                            const idxA = preferredOrder.indexOf(modelA);
                            const idxB = preferredOrder.indexOf(modelB);
                            if (idxA === -1 && idxB === -1) return modelA.localeCompare(modelB);
                            if (idxA === -1) return 1;
                            if (idxB === -1) return -1;
                            return idxA - idxB;
                        })
                        .map(([model, count], idx) => (
                            <tr key={idx}>
                                <td style={td}>{idx + 1}</td>
                                <td style={td}>{model}</td>
                                <td
                                    style={{ ...td, cursor: 'pointer', color: '#0D47A1', fontWeight: 'bold' }}
                                    onClick={() => handleModelCountClick(model)}
                                    role="button"
                                    tabIndex={0}
                                    onKeyDown={(e) => {
                                        if (e.key === 'Enter' || e.key === ' ') {
                                            handleModelCountClick(model);
                                        }
                                    }}
                                >{count}</td>
                            </tr>
                        ))}
                    <tr>
                        <td style={th}></td>
                        <td style={th}><strong>TOTAL</strong></td>
                        <td
                            style={{ ...th, cursor: 'pointer', color: '#0D47A1', fontWeight: 'bold' }}
                            onClick={setTotalCountList}
                            role="button"
                            tabIndex={0}
                            onKeyDown={(e) => {
                                if (e.key === 'Enter' || e.key === ' ') {
                                    setTotalCountList();
                                }
                            }}
                        >
                            <strong>{(Object.values(allCarCount) as number[]).reduce((a: number, b: number) => a + b, 0)}</strong>
                        </td>
                    </tr>
                </tbody>
            </table>

            {/*    Open Contracts */}
            <div style={{
                background: white,
                padding: 16,
                border: `1.5px solid ${purple}`,
                borderRadius: 12,
                minWidth: "320px",
                maxWidth: 500,
                flex: "1 1 40%",
                boxShadow: '0 2px 12px #ffd60022',
                marginTop: 32
            }}>
                <div style={{ display: "flex", justifyContent: "center" }}>
                    <div style={{
                        background: purple,
                        color: yellow,
                        fontWeight: "bold",
                        fontSize: 17,
                        borderRadius: 24,
                        boxShadow: `0 2px 8px #ffd60044`,
                        border: `1.5px solid ${purple}`,
                        padding: "7px 22px",
                        marginBottom: 8,
                        marginTop: 4,
                        display: "inline-block"
                    }}>
                        Open Contracts 
                    </div>
                </div>

                <table style={{ borderCollapse: "collapse", width: "100%", marginBottom: 8 }}>
                    <thead>
                        <tr>
                            <th style={th}>#</th>
                            <th style={th}>Category</th>
                            <th style={th}>Total</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td style={td}>1</td>
                            <td style={td}>Invygo Monthly</td>
                            <td
                                style={{ ...td, cursor: 'pointer', color: '#0D47A1', fontWeight: 'bold' }}
                                onClick={() => handleOpenContractsClick('Invygo')}
                                role="button"
                                tabIndex={0}
                                onKeyDown={(e) => {
                                    if (e.key === 'Enter' || e.key === ' ') {
                                        handleOpenContractsClick('Invygo');
                                    }
                                }}
                            >
                                {data.filter(row => /^\d+$/.test(String(row["Booking Number"] || "").trim())).length}
                            </td>
                        </tr>
                        <tr>
                            <td style={td}>2</td>
                            <td style={td}>Monthly Customers</td>
                            <td
                                style={{ ...td, cursor: 'pointer', color: '#4A148C', fontWeight: 'bold' }}
                                onClick={() => handleOpenContractsClick('Monthly')}
                                role="button"
                                tabIndex={0}
                                onKeyDown={(e) => {
                                    if (e.key === 'Enter' || e.key === ' ') {
                                        handleOpenContractsClick('Monthly');
                                    }
                                }}
                            >
                                {data.filter(row => String(row["Booking Number"] ?? "").toLowerCase().includes("monthly")).length}
                            </td>
                        </tr>
                        <tr>
                            <td style={td}>3</td>
                            <td style={td}>Leasing</td>
                            <td
                                style={{ ...td, cursor: 'pointer', color: '#2E7D32', fontWeight: 'bold' }}
                                onClick={() => handleOpenContractsClick('Leasing')}
                                role="button"
                                tabIndex={0}
                                onKeyDown={(e) => {
                                    if (e.key === 'Enter' || e.key === ' ') {
                                        handleOpenContractsClick('Leasing');
                                    }
                                }}
                            >
                                {data.filter(row => String(row["Booking Number"] || "").toLowerCase().trim() === "leasing").length}
                            </td>
                        </tr>
                        <tr>
                            <td style={td}>4</td>
                            <td style={td}>Daily</td>
                            <td
                                style={{ ...td, cursor: 'pointer', color: '#EF6C00', fontWeight: 'bold' }}
                                onClick={() => handleOpenContractsClick('Daily')}
                                role="button"
                                tabIndex={0}
                                onKeyDown={(e) => {
                                    if (e.key === 'Enter' || e.key === ' ') {
                                        handleOpenContractsClick('Daily');
                                    }
                                }}
                            >
                                {data.filter(row => String(row["Booking Number"] || "").toLowerCase().trim() === "daily").length}
                            </td>
                        </tr>
                        <tr>
                            <td style={th}></td>
                            <td style={th}><strong>TOTAL Rented Business Bay</strong></td>
                            <td style={{ ...th, cursor: 'pointer', color: '#0D47A1', fontWeight: 'bold' }} onClick={() => {
                                // This logic needs to be passed in or handled by parent's total click
                                // For now, assume it's same as total count above or different?
                                // The original code calculated total of all rows with Customer
                            }}>
                                {/* This total cell didn't have a click handler in original code snippet for this specific cell in the text I saw, 
                     but the wrapper did. */}
                                <strong>
                                    {data.filter(row => Object.keys(row).length > 1 && row["Customer"] && row["Customer"]).length}
                                </strong>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
    );
};
