import React, { useState, useEffect } from 'react';
import { th, td, yellow, purpleDark, buttonStyle } from '../styles';
import { formatToDDMMYYYY } from '../utils/dateUtils';
import { useSheetOperations } from '../hooks/useSheetOperations';

export const ContractCheckResults = ({
  contractCheckResults,
  fileData,
  checkFilter,
  setCheckFilter,
  setCopiedContractNo,
  setCopyToast,
  setSelectedContract,
  setShowModal,
  copiedContractNo,
}) => {
  const { loading, writeContractsToSheet, removeContractsFromSheet } = useSheetOperations(setCopyToast);
  const [selectedContracts, setSelectedContracts] = useState<Set<string>>(new Set());

  // Filter results first
  const filteredResults = contractCheckResults
    .filter(r => r.result !== ' OK')
    .filter((r) =>
      r.contract.toLowerCase().includes(checkFilter.toLowerCase()) ||
      r.status.toLowerCase().includes(checkFilter.toLowerCase()) ||
      r.location.toLowerCase().includes(checkFilter.toLowerCase()) ||
      r.result.toLowerCase().includes(checkFilter.toLowerCase())
    );

  // Select all by default when filtered results change
  useEffect(() => {
    setSelectedContracts(new Set(filteredResults.map(r => r.contract)));
  }, [contractCheckResults, checkFilter]);

  if (contractCheckResults.length === 0) return null;

  const handleSelectAll = (e) => {
    if (e.target.checked) {
      setSelectedContracts(new Set(filteredResults.map(r => r.contract)));
    } else {
      setSelectedContracts(new Set());
    }
  };

  const handleSelectOne = (contract) => {
    const newSelected = new Set(selectedContracts);
    if (newSelected.has(contract)) {
      newSelected.delete(contract);
    } else {
      newSelected.add(contract);
    }
    setSelectedContracts(newSelected);
  };

  const getContractsToFixOpen = () => {
    return filteredResults
      .filter(r => selectedContracts.has(r.contract))
      .filter(r => r.result.includes('Missing') && r.status.toLowerCase().includes('open') && r.fullRow)
      .map(r => r.fullRow);
  };

  const getContractsToFixClose = () => {
    return filteredResults
      .filter(r => selectedContracts.has(r.contract))
      .filter(r => (r.result.includes('Error') || (r.result.includes('Missing') && r.status.toLowerCase().includes('close'))) && r.fullRow)
      .map(r => r.fullRow);
  };

  const contractsToFixOpen = getContractsToFixOpen();
  const contractsToFixClose = getContractsToFixClose();


  return (
    <div style={{ marginTop: 40 }}>
      <h3 style={{ color: purpleDark }}> Contract Check Results ({filteredResults.length})</h3>
      <input
        type="text"
        placeholder=" Search results..."
        value={checkFilter}
        onChange={(e) => setCheckFilter(e.target.value)}
        style={{
          padding: '8px',
          width: '20%',
          marginBottom: '16px',
          fontSize: '14px',
          border: '1px solid #ccc',
          borderRadius: '4px',
        }}
      />

      <div style={{ display: 'flex', gap: 16, marginBottom: 16 }}>
        {contractsToFixOpen.length > 0 && (
          <button
            onClick={() => writeContractsToSheet(contractsToFixOpen)}
            disabled={loading}
            style={{
              ...buttonStyle,
              backgroundColor: yellow,
              color: purpleDark,
              border: `2px solid ${purpleDark}`,
              fontSize: '14px',
              padding: '6px 12px',
              opacity: loading ? 0.7 : 1
            }}
          >
            {loading ? ' Fixing...' : ` Fix Missing Open (${contractsToFixOpen.length})`}
          </button>
        )}
        {contractsToFixClose.length > 0 && (
          <button
            onClick={() => removeContractsFromSheet(contractsToFixClose)}
            disabled={loading}
            style={{
              ...buttonStyle,
              backgroundColor: '#ff4444',
              color: 'white',
              border: '2px solid #cc0000',
              fontSize: '14px',
              padding: '6px 12px',
              opacity: loading ? 0.7 : 1
            }}
          >
            {loading ? ' Fixing...' : ` Fix Closed Mismatches (${contractsToFixClose.length})`}
          </button>
        )}
      </div>
      <table style={{ width: '100%', borderCollapse: 'collapse', fontSize: '14px', marginTop: 12 }}>
        <thead>
          <tr style={{ background: yellow, color: purpleDark }}>
            <th style={{ ...th, width: '40px', textAlign: 'center' }}>
              <input
                type="checkbox"
                checked={filteredResults.length > 0 && selectedContracts.size === filteredResults.length}
                onChange={handleSelectAll}
                style={{ cursor: 'pointer', transform: 'scale(1.2)' }}
              />
            </th>
            <th style={th}>#</th>
            <th style={th}>Contract No.</th>
            <th style={th}>Booking Number</th>
            <th style={th}>Customer</th>
            <th style={th}>Pick-up Branch</th>
            <th style={th}>Plate No.</th>
            <th style={th}>Model</th>
            <th style={th}>Pick-up Date</th>
            <th style={th}>Phone Number</th>
            <th style={th}>Drop-off Date</th>
            <th style={th}>Expected Action Date</th>
          </tr>
        </thead>
        <tbody>
          {filteredResults.map((r, i) => {
            const fullData = fileData.find(f =>
              (f['Contract No.'] || f['Agreement'])?.toString().trim().toLowerCase() === r.contract
            ) || {};
            let expectedAction = '';
            if (r.result.trim() === ' Error') {
              expectedAction = 'Should be removed from Open Contract';
            } else if (r.status.toLowerCase().includes('close') && r.result.includes('Missing')) {
              if (fullData['Drop-off Date']) {
                expectedAction = `Should be closed on ${formatToDDMMYYYY(fullData['Drop-off Date'])}`;
              }
            } else if (r.status.toLowerCase().includes('open') && r.result.includes('Missing')) {
              if (fullData['Pick-up Date']) {
                expectedAction = `Should be opened on ${formatToDDMMYYYY(fullData['Pick-up Date'])}`;
              }
            }
            const rowToCopy = [
              r.contract,
              fullData['Booking Number'] ?? '',
              fullData['Customer'] ?? '',
              fullData['Pick-up Branch'] ?? '',
              fullData['Plate No.'] ?? '',
              fullData['Model'] ?? '',
              fullData['Plate No.'] ?? '',
              fullData['Model'] ?? '',
              formatToDDMMYYYY(fullData['Pick-up Date']) ?? '',
              fullData['Phone Number'] ?? ''
            ].join('\t');
            return (
              <tr
                key={i}
                style={{
                  backgroundColor:
                    r.result.trim() === ' Error' ? '#ffcdd2' :
                      r.result.includes('') ? '#fbe9e7' :
                        'white'
                }}
              >
                <td style={{ ...td, textAlign: 'center' }}>
                  <input
                    type="checkbox"
                    checked={selectedContracts.has(r.contract)}
                    onChange={() => handleSelectOne(r.contract)}
                    style={{ cursor: 'pointer', transform: 'scale(1.2)' }}
                  />
                </td>
                <td style={{ ...(td as any), cursor: 'pointer', color: '#1976d2', textDecoration: 'underline' }}
                  onClick={() => {
                    navigator.clipboard.writeText(rowToCopy);
                    setCopiedContractNo(r.contract);
                    setCopyToast('Row copied!');
                    setTimeout(() => setCopyToast(''), 1200);
                  }}
                  title="Click to copy row"
                  role="button"
                  tabIndex={0}
                  onKeyDown={(e) => {
                    if (e.key === 'Enter' || e.key === ' ') {
                      navigator.clipboard.writeText(rowToCopy);
                      setCopiedContractNo(r.contract);
                      setCopyToast('Row copied!');
                      setTimeout(() => setCopyToast(''), 1200);
                    }
                  }}
                >
                  {i + 1}
                </td>
                <td style={{ ...(td as any), cursor: 'pointer', color: copiedContractNo === r.contract ? '#388e3c' : '#1976d2', background: copiedContractNo === r.contract ? '#c8e6c9' : 'inherit' }}
                  onClick={() => {
                    navigator.clipboard.writeText(r.contract);
                    setCopiedContractNo(r.contract);
                  }}
                  title="Click to copy contract number"
                  role="button"
                  tabIndex={0}
                  onKeyDown={(e) => {
                    if (e.key === 'Enter' || e.key === ' ') {
                      navigator.clipboard.writeText(r.contract);
                      setCopiedContractNo(r.contract);
                    }
                  }}
                >
                  {r.contract}
                </td>
                <td style={td}>{fullData['Booking Number']}</td>
                <td style={td}
                  onClick={(e) => {
                    e.stopPropagation();
                    setSelectedContract({ ...r, ...fullData });
                    setShowModal(true);
                  }}
                  title="Show contract details"
                  role="button"
                  tabIndex={0}
                  onKeyDown={(e) => {
                    if (e.key === 'Enter' || e.key === ' ') {
                      e.stopPropagation();
                      setSelectedContract({ ...r, ...fullData });
                      setShowModal(true);
                    }
                  }}
                >
                  {fullData['Customer']}
                </td>
                <td style={td}>{fullData['Pick-up Branch']}</td>
                <td style={td}>{fullData['Plate No.']}</td>
                <td style={td}>{fullData['Model']}</td>
                <td style={td}>{formatToDDMMYYYY(fullData['Pick-up Date'])}</td>
                <td style={td}>{fullData['Phone Number']}</td>
                <td style={td}>{formatToDDMMYYYY(fullData['Drop-off Date'])}</td>
                <td style={{ ...td, fontWeight: 'bold', color: '#d32f2f' }}>{expectedAction}</td>
              </tr>
            );
          })}
        </tbody>
      </table>
    </div>
  );
};
