
import React from 'react';
import { utils, writeFile } from 'xlsx';

interface ReportModalProps {
    showModal: boolean;
    setShowModal: (show: boolean) => void;
    selectedRow: any;
    setSelectedRow: (row: any) => void;
    currentList: any[];
    setCurrentList: (list: any[]) => void;
    rowSource: string;
    copyMsg: string;
    showCopyMsg: (msg: string) => void;
    copyToClipboard: (text: string, msg?: string) => void;
}

export const ReportModal: React.FC<ReportModalProps> = ({
    showModal,
    setShowModal,
    selectedRow,
    setSelectedRow,
    currentList,
    setCurrentList,
    rowSource,
    copyMsg,
    showCopyMsg,
    copyToClipboard
}) => {
    if (!showModal) return null;

    if (selectedRow) {
        return (
            <div
                onClick={() => {
                    setShowModal(false);
                    setSelectedRow(null);
                    setCurrentList([]);
                }}
                role="button"
                tabIndex={0}
                onKeyDown={(e) => {
                    if (e.key === 'Escape') {
                        setShowModal(false);
                        setSelectedRow(null);
                        setCurrentList([]);
                    }
                }}
                style={{
                    position: "fixed",
                    top: 0,
                    left: 0,
                    width: "100%",
                    height: "100%",
                    backgroundColor: "rgba(0,0,0,0.5)",
                    display: "flex",
                    justifyContent: "center",
                    alignItems: "center",
                    zIndex: 9999
                }}
            >
                <div
                    onClick={(e) => e.stopPropagation()}
                    role="none"
                    onKeyDown={(e) => { e.stopPropagation(); }} // Satisfies keyboard listener, though pure layout
                    style={{
                        background: "#fffbea",
                        padding: "24px",
                        borderRadius: "16px",
                        maxWidth: "600px",
                        width: "90%",
                        boxShadow: "0 10px 30px rgba(0,0,0,0.3)",
                        border: "2px solid #800080",
                        fontFamily: "'Segoe UI', sans-serif",
                        textAlign: "center"
                    }}
                >
                    <button
                        onClick={() => setShowModal(false)}
                        style={{
                            position: "absolute",
                            top: 12,
                            right: 12,
                            background: "transparent",
                            border: "none",
                            fontSize: 24,
                            cursor: "pointer",
                            color: "#800080"
                        }}
                    >
                        
                    </button>

                    <div style={{
                        backgroundColor: "#800080",
                        color: "#fff",
                        fontWeight: "bold",
                        fontSize: "18px",
                        padding: "10px 24px",
                        borderRadius: "30px",
                        display: "inline-block",
                        marginBottom: "24px",
                        textAlign: "center"
                    }}>
                        {rowSource === "closed" ? " Closed Contract Details" : " Booking Details"}
                    </div>

                    <table style={{
                        width: "100%",
                        borderCollapse: "collapse",
                        border: "2px solid #800080"
                    }}>
                        <thead>
                            <tr style={{ backgroundColor: "#FFD700" }}>
                                <th style={{
                                    padding: "12px",
                                    border: "1px solid #800080",
                                    color: "#800080",
                                    fontWeight: "bold",
                                    textAlign: "center"
                                }}>
                                    Category
                                </th>
                                <th style={{
                                    padding: "12px",
                                    border: "1px solid #800080",
                                    color: "#800080",
                                    fontWeight: "bold",
                                    textAlign: "center"
                                }}>
                                    Details
                                </th>
                            </tr>
                        </thead>
                        <tbody>
                            {Object.entries(selectedRow)
                                .filter(([_, value]) => value !== null && value !== undefined && value !== "")
                                .map(([key, value]: [string, any], i) => (
                                    <tr key={i} style={{ backgroundColor: i % 2 === 0 ? "#ffffff" : "#fff7d1" }}>
                                        <td style={{
                                            padding: "12px",
                                            border: "1px solid #800080",
                                            color: "#333",
                                            fontWeight: "500",
                                            textAlign: "center",
                                            verticalAlign: "middle"
                                        }}>
                                            {key}
                                        </td>
                                        <td style={{
                                            padding: "12px",
                                            border: "1px solid #800080",
                                            color: "#222",
                                            textAlign: "center",
                                            verticalAlign: "middle"
                                        }}>
                                            {value}
                                        </td>
                                    </tr>
                                ))}
                        </tbody>
                        <tfoot>
                            <tr style={{ backgroundColor: "#FFD700" }}>
                                <td colSpan={2} style={{
                                    padding: "12px",
                                    textAlign: "center",
                                    fontWeight: "bold",
                                    color: "#800080",
                                    border: "1px solid #800080"
                                }}>
                                     End of Booking Info
                                </td>
                            </tr>
                        </tfoot>
                    </table>
                </div>
            </div>
        );
    }

    if (currentList && !selectedRow) {
        return (
            <div
                onClick={() => {
                    setShowModal(false);
                    setSelectedRow(null);
                    setCurrentList([]);
                }}
                role="button"
                tabIndex={0}
                onKeyDown={(e) => {
                    if (e.key === 'Escape') {
                        setShowModal(false);
                        setSelectedRow(null);
                        setCurrentList([]);
                    }
                }}
                style={{
                    position: "fixed",
                    top: 0,
                    left: 0,
                    width: "100vw",
                    height: "100vh",
                    backgroundColor: "rgba(0,0,0,0.5)",
                    display: "flex",
                    justifyContent: "center",
                    alignItems: "center",
                    zIndex: 9999
                }}
            >
                <div
                    onClick={e => e.stopPropagation()}
                    role="none"
                    onKeyDown={(e) => { e.stopPropagation(); }}
                    style={{
                        background: "#fffbea",
                        padding: "24px 12px 24px 12px",
                        borderRadius: "16px",
                        maxWidth: "1200px",
                        width: "98vw",
                        maxHeight: "90vh",
                        overflowY: "auto",
                        boxShadow: "0 10px 30px rgba(0,0,0,0.3)",
                        border: "2px solid #800080",
                        fontFamily: "'Segoe UI', sans-serif",
                        textAlign: "center",
                        position: "relative"
                    }}
                >
                    <button
                        onClick={() => setShowModal(false)}
                        style={{
                            position: "absolute",
                            top: 12,
                            right: 12,
                            background: "transparent",
                            border: "none",
                            fontSize: 24,
                            cursor: "pointer",
                            color: "#800080"
                        }}
                    >
                        
                    </button>
                    <div style={{
                        backgroundColor: "#800080",
                        color: "#fff",
                        fontWeight: "bold",
                        fontSize: "18px",
                        padding: "10px 24px",
                        borderRadius: "30px",
                        display: "inline-block",
                        marginBottom: "24px",
                        textAlign: "center"
                    }}>
                         Open Contracts ({currentList.length} contracts)
                        <button
                            onClick={() => {
                                const ws = utils.json_to_sheet(currentList);
                                const wb = utils.book_new();
                                utils.book_append_sheet(wb, ws, 'Contracts');
                                writeFile(wb, `Contracts_Export_${new Date().toISOString().slice(0, 10)}.xlsx`);
                                showCopyMsg('Excel exported!');
                            }}
                            style={{
                                marginLeft: '16px',
                                background: '#2E7D32',
                                color: '#fff',
                                border: 'none',
                                padding: '6px 12px',
                                borderRadius: '6px',
                                cursor: 'pointer',
                                fontSize: '12px',
                                fontWeight: 'bold'
                            }}
                        >
                             Export Excel
                        </button>
                    </div>
                    <div style={{ overflowX: 'auto', padding: '0 2px 0 2px', position: 'relative' }}>
                        {copyMsg && (
                            <div style={{
                                position: 'absolute',
                                top: 8,
                                left: '50%',
                                transform: 'translateX(-50%)',
                                background: '#800080',
                                color: '#fff',
                                padding: '6px 18px',
                                borderRadius: 16,
                                fontWeight: 'bold',
                                fontSize: 15,
                                zIndex: 10000,
                                boxShadow: '0 2px 8px #80008044',
                                pointerEvents: 'none',
                                opacity: 0.95
                            }}>{copyMsg}</div>
                        )}
                        {currentList.length > 0 ? (
                            <table style={{ width: "100%", borderCollapse: "collapse", border: "2px solid #800080", fontSize: '13px', minWidth: '900px' }}>
                                <thead>
                                    <tr style={{ backgroundColor: "#FFD700" }}>
                                        <th style={{ padding: "8px", border: "1px solid #800080", color: "#800080", fontWeight: "bold", textAlign: "center", background: '#fffbe7', position: 'sticky', top: 0, cursor: 'pointer' }}
                                            onClick={() => copyToClipboard(currentList.map((row, i) => Object.entries(row).filter(([key]) => key.toLowerCase() !== 'replacement' && key.toLowerCase() !== 'replacement date').map(([_, value]) => value).join('\t')).join('\n'), 'All rows copied!')}
                                            role="button"
                                            tabIndex={0}
                                            onKeyDown={(e) => {
                                                if (e.key === 'Enter' || e.key === ' ') {
                                                    copyToClipboard(currentList.map((row, i) => Object.entries(row).filter(([key]) => key.toLowerCase() !== 'replacement' && key.toLowerCase() !== 'replacement date').map(([_, value]) => value).join('\t')).join('\n'), 'All rows copied!');
                                                }
                                            }}
                                            onDoubleClick={() => {
                                                const ws = utils.json_to_sheet(currentList);
                                                const wb = utils.book_new();
                                                utils.book_append_sheet(wb, ws, 'Contracts');
                                                writeFile(wb, `Contracts_Export_${new Date().toISOString().slice(0, 10)}.xlsx`);
                                                showCopyMsg('Excel exported!');
                                            }}
                                            title="Click to copy, Double-click to export Excel"
                                        >#</th>
                                        {Object.keys(currentList[0]).filter(key => key.toLowerCase() !== 'replacement' && key.toLowerCase() !== 'replacement date').map((key, i) => (
                                            <th key={i} style={{ padding: "8px", border: "1px solid #800080", color: "#800080", fontWeight: "bold", textAlign: "center", background: '#fffbe7', position: 'sticky', top: 0 }}>{key}</th>
                                        ))}
                                    </tr>
                                </thead>
                                <tbody>
                                    {currentList.map((row, i) => (
                                        <tr key={i} style={{ backgroundColor: i % 2 === 0 ? "#ffffff" : "#fff7d1", borderBottom: '1px solid #e0e0e0' }}>
                                            <td
                                                style={{ padding: "8px 4px", border: "1px solid #e0e0e0", color: "#800080", textAlign: "center", verticalAlign: "middle", fontWeight: 'bold', cursor: 'pointer', background: '#f3eaff' }}
                                                onClick={() => copyToClipboard(Object.entries(row).filter(([key]) => key.toLowerCase() !== 'replacement' && key.toLowerCase() !== 'replacement date').map(([_, value]) => value).join('\t'), 'Row copied!')}
                                                role="button"
                                                tabIndex={0}
                                                onKeyDown={(e) => {
                                                    if (e.key === 'Enter' || e.key === ' ') {
                                                        copyToClipboard(Object.entries(row).filter(([key]) => key.toLowerCase() !== 'replacement' && key.toLowerCase() !== 'replacement date').map(([_, value]) => value).join('\t'), 'Row copied!');
                                                    }
                                                }}
                                                title="Copy whole row"
                                            >{i + 1}</td>
                                            {Object.entries(row).filter(([key]) => key.toLowerCase() !== 'replacement' && key.toLowerCase() !== 'replacement date').map(([_, value], j) => (
                                                <td
                                                    key={j}
                                                    style={{ padding: "8px 4px", border: "1px solid #e0e0e0", color: "#222", textAlign: "center", verticalAlign: "middle", whiteSpace: 'nowrap', cursor: 'pointer' }}
                                                    onClick={() => copyToClipboard(String(value))}
                                                    role="button"
                                                    tabIndex={0}
                                                    onKeyDown={(e) => {
                                                        if (e.key === 'Enter' || e.key === ' ') {
                                                            copyToClipboard(String(value));
                                                        }
                                                    }}
                                                    title="Copy cell"
                                                >{value}</td>
                                            ))}
                                        </tr>
                                    ))}
                                </tbody>
                            </table>
                        ) : (
                            <div style={{ color: "#800080", fontWeight: "bold", margin: "24px 0" }}>No open contracts for this model.</div>
                        )}
                    </div>
                </div>
            </div>
        );
    }

    return null;
};
