import React from 'react';
import {
  inputStyle,
  buttonStyle,
  controlContainer,
  controlRow,
  controlGroup,
  labelStyle,
  primaryButton,
  secondaryButton,
  cloudButton,
  syncButton
} from '../styles';
import { clearCarsCache, fetchCarsDatabase } from '../utils/carsDatabase';

export const Controls = ({
  startDate,
  setStartDate,
  endDate,
  setEndDate,
  handleFileChange,
  handleInvygoUpload,
  handleReset,
  runContractCheck,
  checkingContracts,
  handleLoadFromCloud,
  handleSaveToCloud,
  loadingCloud
}) => {
  const handleRefreshCarsDB = async () => {
    clearCarsCache();
    await fetchCarsDatabase();
    alert(' Cars database refreshed!');
  };

  return (
    <div style={controlContainer}>

      {/* Top Row: Cloud & System Status */}
      <div style={{ ...controlRow, justifyContent: 'space-between', borderBottom: '1px solid #eee', paddingBottom: '16px' }}>
        <div style={{ display: 'flex', gap: '12px', alignItems: 'center' }}>
          <span style={{ fontSize: '18px', fontWeight: 'bold', color: '#333' }}> Control Panel</span>
        </div>

        <div style={{ display: 'flex', gap: '12px' }}>
          <button
            onClick={handleLoadFromCloud}
            disabled={loadingCloud}
            style={cloudButton}
            title="Load the last saved session from Google Cloud"
          >
            {loadingCloud ? ' Loading...' : ' Load Session'}
          </button>
          <button
            onClick={handleSaveToCloud}
            disabled={loadingCloud}
            style={syncButton}
            title="Save current work to Google Cloud"
          >
            {loadingCloud ? ' Saving...' : ' Save to Cloud'}
          </button>
        </div>
      </div>

      {/* Second Row: Inputs & Actions */}
      <div style={controlRow}>

        {/* Date Filters */}
        <div style={controlGroup}>
          <label htmlFor="startDate" style={labelStyle}>Start Date</label>
          <input id="startDate" type="date" value={startDate} onChange={e => setStartDate(e.target.value)} style={inputStyle} required />
        </div>

        <div style={controlGroup}>
          <label htmlFor="endDate" style={labelStyle}>End Date</label>
          <input id="endDate" type="date" value={endDate} onChange={e => setEndDate(e.target.value)} style={inputStyle} required />
        </div>

        {/* File Imports */}
        <div style={controlGroup}>
          <label htmlFor="ejarFile" style={labelStyle}>EJAR File (XLSX)</label>
          <input id="ejarFile" type="file" accept=".xlsx" onChange={handleFileChange} style={{ ...inputStyle, padding: '7px' }} />
        </div>

        <div style={controlGroup}>
          <label htmlFor="invygoFile" style={labelStyle}>Invygo File (CSV)</label>
          <input id="invygoFile" type="file" accept=".csv" onChange={handleInvygoUpload} style={{ ...inputStyle, padding: '7px' }} />
        </div>
      </div>

      {/* Third Row: Primary Actions */}
      <div style={{ ...controlRow, marginTop: '8px', paddingTop: '16px', borderTop: '1px solid #eee' }}>

        <button
          style={{ ...primaryButton, flex: 1, height: '48px', fontSize: '16px' }}
          onClick={runContractCheck}
          disabled={checkingContracts}
        >
          {checkingContracts ? ' Checking Contracts...' : ' Run Contract Check'}
        </button>

        <button
          style={{ ...buttonStyle, height: '48px' }}
          onClick={handleRefreshCarsDB}
          title="Refresh database"
        >
           Refresh DB
        </button>

        <button
          style={{ ...secondaryButton, height: '48px' }}
          onClick={handleReset}
        >
           Reset All
        </button>

      </div>
    </div>
  );
};