import React, { useState, useEffect } from "react";
import { unifiedModelName } from "./utils/modelUtils";
import { purple, yellow, yellowDark, purpleDark, white } from "./styles";
import { StatisticsPanel } from "./components/StatisticsPanel";
import { CarTable } from "./components/CarTable";
import { ReportModal } from "./components/ReportModal";

// Helper for date padding
const pad = (v: any) => (v ? v.toString().padStart(2, "0") : "00");

export default function DailyBookingReport() {
  const [data, setData] = useState<any[]>([]);
  const [selectedDate, setSelectedDate] = useState(new Date().toISOString().slice(0, 10));
  const [latestInvygoGroup, setLatestInvygoGroup] = useState<any>(null);
  const [loadingInvygo, setLoadingInvygo] = useState(true);
  const [selectedRow, setSelectedRow] = useState<any>(null);
  const [showModal, setShowModal] = useState(false);
  const [rowSource, setRowSource] = useState(""); // 'booked' or 'closed'
  const [currentList, setCurrentList] = useState<any[]>([]); // Open data
  const [currentIndex, setCurrentIndex] = useState(-1);
  const [monthlyClosedGroup, setMonthlyClosedGroup] = useState<any>(null);

  // Copy message state
  const [copyMsg, setCopyMsg] = useState("");
  const showCopyMsg = (msg: string) => {
    setCopyMsg(msg);
    setTimeout(() => setCopyMsg(""), 1200);
  };

  const copyToClipboard = async (text: string, msg = "Copied!") => {
    try {
      await navigator.clipboard.writeText(text);
      showCopyMsg(msg);
    } catch {
      showCopyMsg("Copy failed");
    }
  };

  useEffect(() => {
    const fetchSheet = async () => {
      try {
        const response = await fetch(
          "https://docs.google.com/spreadsheets/d/1XwBko5v8zOdTdv-By8HK_DvZnYT2T12mBw_SIbCfMkE/export?format=csv&gid=769459790"
        );
        const text = await response.text();
        const rows = text.split("\n").map((r) => r.split(","));
        const headers = rows.find((row) => row.some((c) => c.trim() !== ""));
        if (!headers) return;
        const values = rows.slice(rows.indexOf(headers) + 1);
        const parsed = values
          .filter((r) => r.length === headers.length && r.some((c) => c.trim() !== ""))
          .map((r) => Object.fromEntries(r.map((c, i) => [headers[i]?.trim(), c?.trim()])));
        setData(parsed);
      } catch (error) {
        console.error("Error loading report:", error);
      }
    };

    fetchSheet();

    const fetchMonthlyClosed = async () => {
      try {
        const response = await fetch(
          "https://docs.google.com/spreadsheets/d/1XwBko5v8zOdTdv-By8HK_DvZnYT2T12mBw_SIbCfMkE/export?format=csv&gid=375289726"
        );
        const text = await response.text();
        const rows = text.split("\n").map((r) => r.split(","));
        const grouped: any[] = [];
        let currentDate: string | null = null;

        for (const row of rows) {
          const firstCell = row[0]?.trim();
          const isDate = /^\d{2}\/\d{2}\/\d{4}$/.test(firstCell);
          if (isDate) {
            currentDate = firstCell;
            grouped.push({ date: currentDate, contracts: [], source: "monthly" });
          } else if (currentDate && row.some(c => c.trim() !== "")) {
            grouped[grouped.length - 1].contracts.push(row);
          }
        }

        setMonthlyClosedGroup({
          ...grouped[grouped.length - 1],
          allGroups: grouped
        });
      } catch (err) {
        console.error("Failed to load Monthly Closed data:", err);
      }
    };

    fetchMonthlyClosed();

    // Keyboard navigation
    const handleKeyDown = (e: KeyboardEvent) => {
      if (!showModal) return;

      if (e.key === "ArrowRight" && currentIndex < currentList.length - 1) {
        setCurrentIndex((prev) => {
          const nextIndex = prev + 1;
          setSelectedRow(currentList[nextIndex]);
          return nextIndex;
        });
      } else if (e.key === "ArrowLeft" && currentIndex > 0) {
        setCurrentIndex((prev) => {
          const prevIndex = prev - 1;
          setSelectedRow(currentList[prevIndex]);
          return prevIndex;
        });
      }
    };

    window.addEventListener("keydown", handleKeyDown);
    return () => window.removeEventListener("keydown", handleKeyDown);
  }, [showModal, currentIndex, currentList]);

  useEffect(() => {
    const fetchInvygo = async () => {
      try {
        const response = await fetch(
          "https://docs.google.com/spreadsheets/d/1XwBko5v8zOdTdv-By8HK_DvZnYT2T12mBw_SIbCfMkE/export?format=csv&gid=1830448171"
        );
        const text = await response.text();
        const rows = text.split("\n").map((r) => r.split(","));
        const grouped: any[] = [];
        let currentDate: string | null = null;

        for (const row of rows) {
          const firstCell = row[0]?.trim();
          const isDate = /^\d{2}\/\d{2}\/\d{4}$/.test(firstCell);

          if (isDate) {
            currentDate = firstCell;
            grouped.push({ date: currentDate, contracts: [], source: "invygo" });
          } else if (currentDate && row.some((c) => c.trim() !== "")) {
            grouped[grouped.length - 1].contracts.push(row);
          }
        }

        setLatestInvygoGroup({
          ...grouped[grouped.length - 1],
          allGroups: grouped
        });
      } catch (err) {
        console.error("Failed to load INVYGO Closed data:", err);
      } finally {
        setLoadingInvygo(false);
      }
    };

    fetchInvygo();
  }, []);

  // Merge open and closed contracts opened on the same date
  const filteredByDate = (() => {
    // Open contracts
    const openContracts = data.filter((row) => {
      const rawDate = row["Pick-up Date"];
      if (!rawDate) return false;

      const dateStr = rawDate.toString().trim();
      let normalized = "";

      if (dateStr.includes("-")) {
        const datePart = dateStr.match(/^(\d{2})-(\d{2})-(\d{4})/);
        if (datePart) {
          const [, dd, mm, yyyy] = datePart;
          normalized = `${yyyy}-${pad(mm)}-${pad(dd)}`;
        }
      }

      return normalized === selectedDate;
    });

    // Closed contracts from same date
    const closedContractsFromDate: any[] = [];

    // Helper to process groups
    const processGroup = (groups: any[]) => {
      if (!groups) return;
      groups.forEach(group => {
        group.contracts.forEach((row: any[]) => {
          if (row.length > 8) {
            const pickupDate = row[8];
            if (pickupDate) {
              const dateStr = pickupDate.toString().trim();
              let normalized = "";

              if (dateStr.includes("-")) {
                const datePart = dateStr.match(/^(\d{2})-(\d{2})-(\d{4})/);
                if (datePart) {
                  const [, dd, mm, yyyy] = datePart;
                  normalized = `${yyyy}-${pad(mm)}-${pad(dd)}`;
                }
              } else if (dateStr.includes("/")) {
                const datePart = dateStr.match(/^(\d{2})\/(\d{2})\/(\d{4})/);
                if (datePart) {
                  const [, dd, mm, yyyy] = datePart;
                  normalized = `${yyyy}-${pad(mm)}-${pad(dd)}`;
                }
              }

              if (normalized === selectedDate) {
                const contractData = {
                  "Contract No.": row[0] || "",
                  "Booking Number": row[1] || "",
                  "Customer": row[2] || "",
                  "Pick-up Branch": row[3] || "",
                  "EJAR": "",
                  "Model ( Ejar )": "",
                  "INVYGO": row[4] || "",
                  "Model": row[5] || "",
                  "Pick-up Date": row[8] || "",
                  "Phone Number": row[9] || "",
                  "Drop-off Date": ""
                };
                closedContractsFromDate.push(contractData);
              }
            }
          }
        });
      });
    };

    if (latestInvygoGroup?.allGroups) processGroup(latestInvygoGroup.allGroups);
    if (monthlyClosedGroup?.allGroups) processGroup(monthlyClosedGroup.allGroups);

    return [...openContracts, ...closedContractsFromDate];
  })();

  const allCarCount = (() => {
    const grouped: Record<string, number> = {};
    data.forEach((row) => {
      let booking = row["Booking Number"];
      if (!booking) return;
      booking = String(booking).trim();
      if (!booking.match(/^\d+$/)) return;
      let model = unifiedModelName(row["Model"] || "");
      grouped[model] = (grouped[model] || 0) + 1;
    });
    return grouped;
  })();

  const handleOpenContractsClick = (category: string) => {
    let filtered: any[] = [];
    if (category === 'Invygo') {
      filtered = data.filter(row => /^\d+$/.test(String(row["Booking Number"] || "").trim()));
    } else if (category === 'Monthly') {
      filtered = data.filter(row => String(row["Booking Number"] ?? "").toLowerCase().includes("monthly"));
    } else if (category === 'Leasing') {
      filtered = data.filter(row => String(row["Booking Number"] || "").toLowerCase().trim() === "leasing");
    } else if (category === 'Daily') {
      filtered = data.filter(row => String(row["Booking Number"] || "").toLowerCase().trim() === "daily");
    }
    setCurrentList(filtered);
    setSelectedRow(null);
    setShowModal(true);
  };

  const handleModelCountClick = (model: string) => {
    const filtered = data.filter(row =>
      unifiedModelName(row['Model'] || "") === model &&
      String(row["Booking Number"] || "").trim().match(/^\d+$/)
    );
    setCurrentList(filtered);
    setSelectedRow(null);
    setShowModal(true);
  };

  const invygoClosedColumns = [
    "Contract No.",
    "Booking Number",
    "Customer",
    "Pick-up Branch",
    "Plate No.",
    "Model",
    "Plate No. 2",
    "Model 2",
    "Pick-up Date",
    "Contact"
  ];

  const formatInvygoDate = (dateStr: string) => {
    if (!dateStr) return "";
    const [dd, mm, yyyy] = dateStr.split("/");
    return `${yyyy}-${mm.padStart(2, "0")}-${dd.padStart(2, "0")}`;
  };

  const allClosedGroups = [
    ...(latestInvygoGroup?.allGroups || []),
    ...(monthlyClosedGroup?.allGroups || [])
  ];

  const selectedClosedGroups = allClosedGroups.filter(
    group => formatInvygoDate(group.date) === selectedDate
  );

  const preferredOrder = [
    "Mitsubishi Attrage",
    "Geely Emgrand 2024",
    "MG 5 2024",
    "MG 5 2025",
    "Kia Pegas 2024",
    "Chery Tiggo 2025",
    "Peugeot 2008 2025",
    "Peugeot 208 2025",
    "Peugeot 408 2025/2026",
    "Chery Arrizo 5 2025",
    "Peugeot 2008 Active 2025"
  ];

  const setTotalCountList = () => {
    const allInvygoCars = data.filter(row => /^\d+$/.test(String(row["Booking Number"] || "").trim()));
    setCurrentList(allInvygoCars);
    setSelectedRow(null);
    setShowModal(true);
  };

  return (
    <div style={{ padding: 24, fontFamily: "Cairo, Arial, sans-serif", background: "#fffbe7", minHeight: "100vh" }}>
      <div style={{
        backgroundColor: yellow,
        color: purpleDark,
        border: `2.5px solid ${purple}`,
        borderRadius: 16,
        padding: '12px 32px',
        fontWeight: 'bold',
        fontSize: '24px',
        margin: '0 auto 32px auto',
        textAlign: 'center',
        boxShadow: '0 2px 12px #ffd60044',
        maxWidth: 500
      }}>
         Rented Cars Report
      </div>

      <div style={{ marginBottom: 18, textAlign: "center" }}>
        <input
          type="date"
          value={selectedDate}
          onChange={(e) => setSelectedDate(e.target.value)}
          style={{
            padding: '10px',
            borderRadius: 8,
            border: `1.5px solid ${purple}`,
            fontSize: '14px',
            marginTop: '4px',
            marginBottom: '4px'
          }}
        />
      </div>

      <div style={{
        display: "flex",
        gap: "32px",
        flexWrap: "wrap",
        justifyContent: "center",
        alignItems: "flex-start",
        marginTop: 32
      }}>
        <StatisticsPanel
          allCarCount={allCarCount}
          preferredOrder={preferredOrder}
          handleModelCountClick={handleModelCountClick}
          data={data}
          handleOpenContractsClick={handleOpenContractsClick}
          setTotalCountList={setTotalCountList}
        />

        <CarTable
          filteredByDate={filteredByDate}
          selectedClosedGroups={selectedClosedGroups}
          invygoClosedColumns={invygoClosedColumns}
          onRowClick={(row, source) => {
            setSelectedRow(row);
            setCurrentList([]);
            setRowSource(source);
            setShowModal(true);
          }}
          selectedDate={selectedDate}
          loadingInvygo={loadingInvygo}
        />
      </div>

      <div style={{ position: "absolute", top: 40, left: 20 }}>
        <a
          href="https://moalamir52.github.io/Yelo/#dashboard"
          style={{
            backgroundColor: yellow,
            color: purpleDark,
            padding: "10px 22px",
            textDecoration: "none",
            fontWeight: "bold",
            borderRadius: "8px",
            border: `2px solid ${purple}`,
            fontSize: '15px',
            boxShadow: '0 2px 12px #ffd60044',
            transition: 'all 0.2s'
          }}
          onMouseOver={e => (e.currentTarget.style.backgroundColor = yellowDark)}
          onMouseOut={e => (e.currentTarget.style.backgroundColor = yellow)}
          onFocus={e => (e.currentTarget.style.backgroundColor = yellowDark)}
          onBlur={e => (e.currentTarget.style.backgroundColor = yellow)}
        >
           Back to Dashboard
        </a>
      </div>

      <ReportModal
        showModal={showModal}
        setShowModal={setShowModal}
        selectedRow={selectedRow}
        setSelectedRow={setSelectedRow}
        currentList={currentList}
        setCurrentList={setCurrentList}
        rowSource={rowSource}
        copyMsg={copyMsg}
        showCopyMsg={showCopyMsg}
        copyToClipboard={copyToClipboard}
      />
    </div>
  );
}
