import React, { useState, useEffect } from 'react';
import { buttonStyle, yellow, purpleDark, yellowDark } from '../styles';
import { useSheetOperations } from '../hooks/useSheetOperations';

interface SheetsButtonsProps {
  openedContracts: any[];
  closedContracts: any[];
  onResult: (message: string) => void;
}

export const SheetsButtons: React.FC<SheetsButtonsProps> = ({
  openedContracts,
  closedContracts,
  onResult
}) => {
  const { loading, extensionInstalled, writeContractsToSheet, removeContractsFromSheet } = useSheetOperations(onResult);

  const handleWriteToSheet = () => {
    writeContractsToSheet(openedContracts);
  };

  const handleRemoveFromSheet = () => {
    removeContractsFromSheet(closedContracts);
  };

  return (
    <div style={{ display: 'flex', gap: 12, marginBottom: 16, flexDirection: 'column' }}>
      {!extensionInstalled && (
        <div style={{
          backgroundColor: '#fff3cd',
          border: '1px solid #ffeaa7',
          borderRadius: 8,
          padding: 12,
          fontSize: 14,
          color: '#856404'
        }}>
           To enable automatic writing, install Chrome Extension from folder: chrome-extension
        </div>
      )}

      <div style={{ display: 'flex', gap: 12 }}>
        <button
          onClick={handleWriteToSheet}
          disabled={loading || !openedContracts.length}
          style={{
            ...buttonStyle,
            backgroundColor: yellow,
            color: purpleDark,
            border: `2px solid ${purpleDark}`,
            opacity: loading || !openedContracts.length ? 0.6 : 1
          }}
          onMouseOver={e => !loading && openedContracts.length && (e.currentTarget.style.backgroundColor = yellowDark)}
          onMouseOut={e => !loading && openedContracts.length && (e.currentTarget.style.backgroundColor = yellow)}
          onFocus={e => !loading && openedContracts.length && (e.currentTarget.style.backgroundColor = yellowDark)}
          onBlur={e => !loading && openedContracts.length && (e.currentTarget.style.backgroundColor = yellow)}
        >
          {loading ? ' Writing...' : ` Write to Sheet (${openedContracts.length})`}
        </button>

        <button
          onClick={handleRemoveFromSheet}
          disabled={loading || !closedContracts.length}
          style={{
            ...buttonStyle,
            backgroundColor: '#ff4444',
            color: 'white',
            border: '2px solid #cc0000',
            opacity: loading || !closedContracts.length ? 0.6 : 1
          }}
          onMouseOver={e => !loading && closedContracts.length && (e.currentTarget.style.backgroundColor = '#cc0000')}
          onMouseOut={e => !loading && closedContracts.length && (e.currentTarget.style.backgroundColor = '#ff4444')}
          onFocus={e => !loading && closedContracts.length && (e.currentTarget.style.backgroundColor = '#cc0000')}
          onBlur={e => !loading && closedContracts.length && (e.currentTarget.style.backgroundColor = '#ff4444')}
        >
          {loading ? ' Removing...' : ` Remove Closed (${closedContracts.length})`}
        </button>
      </div>
    </div>
  );
};