import { useState, useEffect } from 'react';
import { isDailyContract, formatToDDMMYYYY as formatDateForArchive } from '../utils/dateUtils';

export const useSheetOperations = (onResult: (message: string) => void) => {
    const [loading, setLoading] = useState(false);
    const [extensionInstalled, setExtensionInstalled] = useState(true);

    useEffect(() => {
        // Check extension
        const checkExtension = () => {
            window.postMessage({ type: 'CHECK_EXTENSION' }, '*');
            setTimeout(() => setExtensionInstalled(true), 100);
        };
        checkExtension();

        // Listen for extension messages
        const handleMessage = (event: MessageEvent) => {
            if (event.data.type === 'SHEET_WRITE_SUCCESS') {
                setLoading(false);
                onResult(`Successfully wrote ${event.data.count} contracts to sheet!`);
            } else if (event.data.type === 'SHEET_WRITE_ERROR') {
                setLoading(false);
                onResult(`Error: ${event.data.error}`);
            } else if (event.data.type === 'SHEET_REMOVE_SUCCESS') {
                setLoading(false);
                onResult(`Successfully removed ${event.data.count} contracts from sheet`);
            } else if (event.data.type === 'SHEET_REMOVE_ERROR') {
                setLoading(false);
                onResult(`Error: ${event.data.error}`);
            }
        };

        window.addEventListener('message', handleMessage);
        return () => window.removeEventListener('message', handleMessage);
    }, [onResult]);

    const writeContractsToSheet = async (contractsToWrite: any[]) => {
        if (!contractsToWrite.length) {
            onResult('No contracts to write');
            return;
        }
        setLoading(true);

        const rows = contractsToWrite.map(row => {
            const bookingNumber = (() => {
                const val = row['Booking Number'];
                if (val && String(val).trim().toLowerCase().startsWith('c')) return 'Leasing';
                if (!val) {
                    if (row['Drop-off Date'] && isDailyContract(row['Pick-up Date'], row['Drop-off Date'])) {
                        return 'Daily';
                    }
                    return 'Monthly';
                }
                return val;
            })();

            return [
                row['Contract No.'] || '',
                bookingNumber,
                row['Customer'] || '',
                row['Pick-up Branch'] || '',
                row['Plate No.'] || '',
                row['Model'] || '',
                row['Plate No.'] || '',
                row['Model'] || '',
                formatDateForArchive(row['Pick-up Date']),
                row['Phone Number'] || '',
                formatDateForArchive(row['Drop-off Date'])
            ];
        });

        const scriptUrl = process.env.REACT_APP_APPS_SCRIPT_URL;
        if (!scriptUrl) {
            onResult('Error: Configuration Error. REACT_APP_APPS_SCRIPT_URL is missing.');
            setLoading(false);
            return;
        }

        const postForm = document.createElement('form');
        postForm.method = 'POST';
        postForm.action = scriptUrl;
        postForm.target = 'hidden_iframe_' + Date.now();
        postForm.style.display = 'none';

        const actionInput = document.createElement('input');
        actionInput.name = 'action';
        actionInput.value = 'writeOpened';
        postForm.appendChild(actionInput);

        const dataInput = document.createElement('input');
        dataInput.name = 'data';
        dataInput.value = JSON.stringify(rows);
        postForm.appendChild(dataInput);

        const iframe = document.createElement('iframe');
        iframe.name = postForm.target;
        iframe.style.display = 'none';

        iframe.onload = () => {
            setTimeout(() => {
                document.body.removeChild(postForm);
                document.body.removeChild(iframe);
                setLoading(false);
                onResult(`Successfully wrote ${rows.length} contracts to sheet!`);
            }, 3000);
        };

        document.body.appendChild(iframe);
        document.body.appendChild(postForm);
        postForm.submit();
    };

    const removeContractsFromSheet = async (closedContracts: any[]) => {
        if (!closedContracts.length) {
            onResult('No closed contracts to remove');
            return;
        }

        setLoading(true);
        const contractNos = closedContracts.map(c => c['Contract No.'] || c['Agreement']).filter(Boolean);

        function isInvygo(val: any): boolean {
            if (!val) return false;
            const str = String(val).trim();
            return str.toLowerCase().includes('vrd') || /^\d{6,}$/.test(str) || str.toLowerCase().includes('invygo');
        }

        // Separate Invygo and Monthly/Daily contracts
        const invygoContracts = closedContracts.filter(row => isInvygo(row['Booking Number']));
        const monthlyContracts = closedContracts.filter(row => !isInvygo(row['Booking Number']));

        // Prepare data for both sheets
        const prepareContractsByDate = (contracts: any[]): any[][] => {
            const contractsByDate: { [key: string]: any[] } = {};
            contracts.forEach(contract => {
                const dropOffDate = contract['Drop-off Date'];
                if (dropOffDate) {
                    const dateOnly = dropOffDate instanceof Date ? formatDateForArchive(dropOffDate).split(' ')[0] : dropOffDate.split(' ')[0];
                    const dateKey = dateOnly; // Simplified for now
                    if (!contractsByDate[dateKey]) {
                        contractsByDate[dateKey] = [];
                    }
                    contractsByDate[dateKey].push(contract);
                }
            });

            const sortedDates = Object.keys(contractsByDate).sort((a, b) => {
                const dateA = new Date(a.split('/').reverse().join('-'));
                const dateB = new Date(b.split('/').reverse().join('-'));
                return dateA.getTime() - dateB.getTime();
            });

            const rows: any[][] = [];

            sortedDates.forEach(date => {
                rows.push([date, '', '', '', '', '', '', '', '', '', '']);

                contractsByDate[date].forEach(row => {
                    const bookingNumber = (() => {
                        const val = row['Booking Number'];
                        if (val && String(val).trim().toLowerCase().startsWith('c')) return 'Leasing';
                        if (!val) {
                            if (row['Drop-off Date'] && isDailyContract(row['Pick-up Date'], row['Drop-off Date'])) {
                                return 'Daily';
                            }
                            return 'Monthly';
                        }
                        return val;
                    })();

                    rows.push([
                        row['Contract No.'] || '',
                        bookingNumber,
                        row['Customer'] || '',
                        row['Pick-up Branch'] || '',
                        row['Plate No.'] || '',
                        row['Model'] || '',
                        row['Plate No.'] || '',
                        row['Model'] || '',
                        formatDateForArchive(row['Pick-up Date']),
                        row['Phone Number'] || '',
                        formatDateForArchive(row['Drop-off Date'])
                    ]);
                });
            });

            return rows;
        };

        const invygoRows = prepareContractsByDate(invygoContracts);
        const monthlyRows = prepareContractsByDate(monthlyContracts);

        const scriptUrl = process.env.REACT_APP_APPS_SCRIPT_URL;
        if (!scriptUrl) {
            onResult('Error: Configuration Error. REACT_APP_APPS_SCRIPT_URL is missing.');
            setLoading(false);
            return;
        }

        const postForm = document.createElement('form');
        postForm.method = 'POST';
        postForm.action = scriptUrl;
        postForm.target = 'hidden_iframe_remove_' + Date.now();
        postForm.style.display = 'none';

        const actionInput = document.createElement('input');
        actionInput.name = 'action';
        actionInput.value = 'removeAndArchive';
        postForm.appendChild(actionInput);

        const dataInput = document.createElement('input');
        dataInput.name = 'contractsToRemove';
        dataInput.value = JSON.stringify(contractNos);
        postForm.appendChild(dataInput);

        const invygoInput = document.createElement('input');
        invygoInput.name = 'invygoData';
        invygoInput.value = JSON.stringify(invygoRows);
        postForm.appendChild(invygoInput);

        const monthlyInput = document.createElement('input');
        monthlyInput.name = 'monthlyData';
        monthlyInput.value = JSON.stringify(monthlyRows);
        postForm.appendChild(monthlyInput);

        const iframe = document.createElement('iframe');
        iframe.name = postForm.target;
        iframe.style.display = 'none';

        iframe.onload = () => {
            setTimeout(() => {
                document.body.removeChild(postForm);
                document.body.removeChild(iframe);
                setLoading(false);
                onResult(`Successfully removed ${contractNos.length} contracts and archived ${invygoContracts.length} Invygo + ${monthlyContracts.length} Monthly/Daily contracts!`);
            }, 3000);
        };

        document.body.appendChild(iframe);
        document.body.appendChild(postForm);
        postForm.submit();
    };

    const saveToCloud = async (data: any[]) => {
        setLoading(true);
        // eslint-disable-next-line no-console
        console.log(' [Cloud] Starting upload...', { records: data.length });
        try {
            const scriptUrl = process.env.REACT_APP_APPS_SCRIPT_URL;
            if (!scriptUrl) throw new Error('Script URL not found');

            // Convert to JSON string
            const jsonString = JSON.stringify(data);
            // eslint-disable-next-line no-console
            console.log(` [Cloud] Payload size: ${(jsonString.length / 1024).toFixed(2)} KB`);

            const iframeId = 'hidden_iframe_cloud_' + Date.now();
            const iframe = document.createElement('iframe');
            iframe.name = iframeId;
            iframe.style.display = 'none';
            document.body.appendChild(iframe);

            const form = document.createElement('form');
            form.method = 'POST';
            form.action = scriptUrl;
            form.target = iframeId;
            form.style.display = 'none';

            const actionInput = document.createElement('input');
            actionInput.name = 'action';
            actionInput.value = 'saveCache';
            form.appendChild(actionInput);

            const dataInput = document.createElement('input');
            dataInput.name = 'data';
            dataInput.value = jsonString;
            form.appendChild(dataInput);

            document.body.appendChild(form);

            const submitPromise = new Promise<void>((resolve) => {
                const handler = () => {
                    iframe.removeEventListener('load', handler);
                    resolve();
                };
                iframe.addEventListener('load', handler);
                form.submit();
            });

            await submitPromise;
            document.body.removeChild(form);
            if (document.body.contains(iframe)) {
                document.body.removeChild(iframe);
            }

            // eslint-disable-next-line no-console
            console.log(' [Cloud] Upload complete!');
            onResult(' Saved to Cloud Successfully!');
        } catch (error) {
            console.error(' [Cloud] Save error:', error);
            onResult(' Failed to save to cloud');
        } finally {
            setLoading(false);
        }
    };

    const loadFromCloud = async (): Promise<any[] | null> => {
        setLoading(true);
        // eslint-disable-next-line no-console
        console.log(' [Cloud] Starting download...');
        try {
            const scriptUrl = process.env.REACT_APP_APPS_SCRIPT_URL;
            if (!scriptUrl) throw new Error('Script URL not found');

            const response = await fetch(scriptUrl, {
                method: 'POST',
                body: new URLSearchParams({ action: 'getCache' })
            });

            const result = await response.json();

            if (result.status === 'success' && result.data) {
                const data = JSON.parse(result.data);
                // eslint-disable-next-line no-console
                console.log(' [Cloud] Download complete!', { records: data.length });
                onResult(' Data Loaded from Cloud!');
                return data;
            } else {
                // eslint-disable-next-line no-console
                console.warn(' [Cloud] No data found.');
                onResult(' No saved session found.');
                return null;
            }
        } catch (error) {
            // eslint-disable-next-line no-console
            console.error(' [Cloud] Load error:', error);
            onResult(' Failed to load from cloud (CORS/Network)');
            return null;
        } finally {
            setLoading(false);
        }
    };

    return {
        loading,
        extensionInstalled,
        writeContractsToSheet,
        removeContractsFromSheet,
        saveToCloud,
        loadFromCloud
    };
};
