
import React from 'react';
import { th, td, white, purple, yellow, purpleDark } from '../styles';

interface CarTableProps {
    filteredByDate: any[];
    selectedClosedGroups: any[];
    invygoClosedColumns: string[];
    onRowClick: (row: any, source: string) => void;
    selectedDate: string;
    loadingInvygo: boolean;
}

const getBookingColor = (type: string) => {
    switch (type.toLowerCase()) {
        case "invygo": return "#0D47A1";
        case "monthly": return "#4A148C";
        case "daily": return "#EF6C00";
        case "leasing": return "#2E7D32";
        default: return "#999";
    }
};

export const CarTable: React.FC<CarTableProps> = ({
    filteredByDate,
    selectedClosedGroups,
    invygoClosedColumns,
    onRowClick,
    selectedDate,
    loadingInvygo
}) => {
    return (
        <div style={{
            display: "flex",
            flexDirection: "column",
            gap: "32px",
            minWidth: "400px",
            flex: "1 1 40%",
            alignItems: "stretch"
        }}>
            {/*  Booked Cars */}
            <div style={{
                background: white,
                padding: 16,
                border: `1.5px solid ${purple}`,
                borderRadius: 12,
                boxShadow: '0 2px 12px #ffd60022'
            }}>
                {/*  Booked Cars */}
                <div style={{ display: "flex", justifyContent: "center" }}>
                    <div style={{
                        background: purple,
                        color: yellow,
                        fontWeight: "bold",
                        fontSize: 17,
                        borderRadius: 24,
                        boxShadow: `0 2px 8px #ffd60044`,
                        border: `1.5px solid ${purple}`,
                        padding: "7px 22px",
                        marginBottom: 8,
                        marginTop: 4,
                        display: "inline-block"
                    }}>
                        Booked Cars  {selectedDate}
                    </div>
                </div>
                <table style={{ borderCollapse: "collapse", width: "100%", marginBottom: 8 }}>
                    <thead>
                        <tr>
                            <th style={th}>#</th>
                            <th style={th}>Model</th>
                            <th style={th}>Plate Number</th>
                        </tr>
                    </thead>
                    <tbody>
                        {filteredByDate.map((row, idx) => {
                            const bookingRaw = String(row["Booking Number"] || "").trim().toLowerCase();
                            let type = "Unknown";
                            if (/^\d+$/.test(bookingRaw)) type = "Invygo";
                            else if (bookingRaw === "daily") type = "Daily";
                            else if (bookingRaw === "leasing") type = "Leasing";
                            else if (bookingRaw.includes("monthly")) type = "Monthly";

                            const color = getBookingColor(type);

                            return (
                                <tr
                                    key={idx}
                                    onClick={() => onRowClick(row, "booked")}
                                    role="button"
                                    tabIndex={0}
                                    onKeyDown={(e) => {
                                        if (e.key === 'Enter' || e.key === ' ') {
                                            e.preventDefault();
                                            onRowClick(row, "booked");
                                        }
                                    }}
                                    style={{ cursor: "pointer", backgroundColor: "#f9f9f9" }}
                                >
                                    <td style={td}>{idx + 1}</td>
                                    <td style={{ ...td, textAlign: 'center' }}>
                                        <div style={{ display: 'inline-flex', gap: '16px', alignItems: 'center', justifyContent: 'center' }}>
                                            <span>{row["Model"]}</span>
                                            <span style={{
                                                border: `1.5px solid ${color}`,
                                                color: color,
                                                padding: "2px 10px",
                                                borderRadius: "12px",
                                                fontSize: "12px",
                                                fontWeight: "bold",
                                                whiteSpace: "nowrap"
                                            }}>
                                                {type}
                                            </span>
                                        </div>
                                    </td>
                                    <td style={td}>{row["INVYGO"] || row["EJAR"] || ""}</td>
                                </tr>
                            );
                        })}
                    </tbody>
                </table>
            </div>

            {/*  INVYGO Closed Contracts */}
            <div style={{
                background: white,
                padding: 16,
                border: `1.5px solid ${purple}`,
                borderRadius: 12,
                boxShadow: '0 2px 12px #ffd60022'
            }}>
                <div style={{ display: "flex", justifyContent: "center" }}>
                    <div style={{
                        background: purple,
                        color: yellow,
                        fontWeight: "bold",
                        fontSize: 17,
                        borderRadius: 24,
                        boxShadow: `0 2px 8px #ffd60044`,
                        border: `1.5px solid ${purple}`,
                        padding: "7px 22px",
                        marginBottom: 8,
                        marginTop: 4,
                        display: "inline-block"
                    }}>
                        INVYGO Closed Contracts 
                    </div>
                </div>
                <table style={{ borderCollapse: "collapse", width: "100%", marginBottom: 8 }}>
                    <thead>
                        <tr>
                            <th style={th}>#</th>
                            <th style={th}>Model</th>
                            <th style={th}>Plate Number</th>
                        </tr>
                    </thead>
                    <tbody>
                        {selectedClosedGroups.length > 0 ? (
                            selectedClosedGroups.map((group, gIdx) => {
                                return group.contracts.map((row, idx) => {
                                    const bookingRaw = String(row[1] || '').trim().toLowerCase();
                                    let type = "Unknown";
                                    if (/^\d+$/.test(bookingRaw)) type = "Invygo";
                                    else if (bookingRaw === "daily") type = "Daily";
                                    else if (bookingRaw === "leasing") type = "Leasing";
                                    else if (bookingRaw.includes("monthly")) type = "Monthly";

                                    const bookingColor = getBookingColor(type);
                                    const model = row[5] || "";
                                    const plate = row[4] || "";

                                    const rowData = {};
                                    invygoClosedColumns.forEach((col, i) => {
                                        rowData[col] = row[i] || "";
                                    });

                                    return (
                                        <tr
                                            key={`row-${gIdx}-${idx}`}
                                            onClick={() => onRowClick(rowData, "closed")}
                                            role="button"
                                            tabIndex={0}
                                            onKeyDown={(e) => {
                                                if (e.key === 'Enter' || e.key === ' ') {
                                                    e.preventDefault();
                                                    onRowClick(rowData, "closed");
                                                }
                                            }}
                                            style={{ cursor: "pointer", backgroundColor: "#fdfbe3" }}
                                        >
                                            <td style={td}>{(() => {
                                                let counter = 0;
                                                for (let g = 0; g < gIdx; g++) {
                                                    counter += selectedClosedGroups[g].contracts.length;
                                                }
                                                return counter + idx + 1;
                                            })()}</td>
                                            <td style={td}>
                                                {model} <span style={{ border: `1px solid ${bookingColor}`, padding: "2px 6px", borderRadius: 8, marginLeft: 6, fontSize: "12px", fontWeight: "bold", color: bookingColor }}>{type}</span>
                                            </td>
                                            <td style={td}>{plate}</td>
                                        </tr>
                                    );
                                });
                            })
                        ) : (
                            <tr>
                                <td style={td} colSpan={3}>No closed contracts for this date.</td>
                            </tr>
                        )}
                    </tbody>
                </table>
            </div>

            {/* Main INVYGO Closed Contracts Table */}
            <div style={{
                marginTop: 40,
                background: white,
                border: `1.5px solid ${purple}`,
                borderRadius: 12,
                boxShadow: '0 2px 12px #ffd60022',
                padding: 18
            }}>
                <div style={{ display: "flex", justifyContent: "center" }}>
                    <div style={{
                        background: purple,
                        color: yellow,
                        fontWeight: "bold",
                        fontSize: 17,
                        borderRadius: 24,
                        boxShadow: `0 2px 8px #ffd60044`,
                        border: `1.5px solid ${purple}`,
                        padding: "7px 22px",
                        marginBottom: 8,
                        marginTop: 4,
                        display: "inline-block"
                    }}>
                        INVYGO Closed Contracts 
                    </div>
                </div>

                {loadingInvygo ? (
                    <p style={{ textAlign: "center", color: purpleDark }}>Loading INVYGO Closed contracts...</p>
                ) : (
                    <div style={{ marginBottom: 30 }}>
                        <table style={{ borderCollapse: "collapse", width: "100%" }}>
                            <thead>
                                <tr>
                                    <th
                                        colSpan={invygoClosedColumns.length + 1}
                                        style={{
                                            ...th,
                                            background: yellow,
                                            color: purpleDark,
                                            fontWeight: 'bold',
                                            fontSize: '17px',
                                            borderTopLeftRadius: 12,
                                            borderTopRightRadius: 12,
                                            textAlign: 'center',
                                            padding: "12px 0"
                                        }}
                                    >
                                        Date: {selectedClosedGroups.length > 0 ? selectedClosedGroups[0].date : selectedDate}
                                    </th>
                                </tr>
                                <tr>
                                    <th style={th}>#</th>
                                    {invygoClosedColumns.map((col, idx) => (
                                        <th key={idx} style={th}>{col}</th>
                                    ))}
                                </tr>
                            </thead>
                            <tbody>
                                {selectedClosedGroups.length > 0 ? (
                                    selectedClosedGroups.map((group, gIdx) => (
                                        <React.Fragment key={`group-${gIdx}`}>
                                            {group.contracts.length > 0 ? (
                                                group.contracts.map((row, idx) => (
                                                    <tr key={`row-${gIdx}-${idx}`}>
                                                        <td style={td}>{(() => {
                                                            let counter = 0;
                                                            for (let g = 0; g < gIdx; g++) {
                                                                counter += selectedClosedGroups[g].contracts.length;
                                                            }
                                                            return counter + idx + 1;
                                                        })()}</td>
                                                        {invygoClosedColumns.map((_, i) => (
                                                            <td key={`col-${i}`} style={td}>{row[i] || ""}</td>
                                                        ))}
                                                    </tr>
                                                ))
                                            ) : (
                                                <tr key={`empty-${gIdx}`}>
                                                    <td style={td}></td>
                                                    {invygoClosedColumns.map((_, idx) => (
                                                        <td key={`col-${idx}`} style={td}></td>
                                                    ))}
                                                </tr>
                                            )}
                                        </React.Fragment>
                                    ))
                                ) : (
                                    <tr>
                                        <td colSpan={invygoClosedColumns.length + 1} style={td}>No closed contracts found for this date.</td>
                                    </tr>
                                )}
                            </tbody>
                        </table>
                    </div>
                )}
            </div>
        </div>
    );
};
